import React, { useState, useEffect } from 'react'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft, ChevronsLeft, ChevronsRight
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, Table, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, Modal, ModalHeader, ModalBody, ModalFooter
} from 'reactstrap'
import { CURRENCY_SYMBOL } from '../../../Configuarables'
import { formatReadableDate } from '../../../helper'
import Pdf from "react-to-pdf"
const ref = React.createRef()

const InvoiceModal = ({ InvoiceDetails, InvModal, setInvModal }) => {
    useEffect(() => {
        console.log('row', InvoiceDetails)
    }, [])
    return (
        <Modal

            isOpen={InvModal}
            toggle={() => setInvModal(!InvModal)}
            className={`modal-dialog-centered modal-lg`}
        >
            <ModalHeader style={{ width: '835px' }} toggle={() => setInvModal(!InvModal)}>
                <div >
                    <Pdf targetRef={ref} filename="code-example.pdf">
                        {({ toPdf }) => <Button.Ripple color='primary' type="button" onClick={toPdf}>
                            Download as Pdf
                            {/* <button onClick={toPdf}>Generate Pdf</button> */}
                        </Button.Ripple>}
                    </Pdf>

                </div>

            </ModalHeader>
            <ModalBody style={{ width: '835px', background: 'white' }}>
                <div ref={ref} >
                    <Card style={{ width: '800px' }}>
                        <h1 className="text-primary"> <b>i-Prop</b></h1>
                        <br />
                        <h3 className="text-primary text-center"> <b> Invoice Details</b></h3>
                        <br />
                        <div className="text-center">
                            <Badge pill color={'warning'} className='px-2 py-1'>
                                Current Status :  {InvoiceDetails.statusType.status_name}
                            </Badge>
                        </div>
                        <br />
                        <Table responsive bordered >
                            <thead >
                                <tr >
                                    <th className='bg-white'>Invoice ID</th>
                                    <th className='bg-white'>Issue Date</th>
                                    <th className='bg-white'>Total Amount</th>
                                    <th className='bg-white'>Paid Amount</th>
                                    <th className='bg-white'>Attachment</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <th scope="row">{InvoiceDetails.invoice_id}</th>
                                    <td>{formatReadableDate(InvoiceDetails.issue_date)}</td>
                                    <td>{CURRENCY_SYMBOL} {InvoiceDetails.total_amount}</td>
                                    <td>{CURRENCY_SYMBOL} {InvoiceDetails.pay_amount}</td>
                                    <td>
                                        <a href={InvoiceDetails.payment_attachment} download>
                                            <img src={InvoiceDetails.payment_attachment} alt="Not found!" width="50" height="50" />
                                        </a>
                                    </td>
                                </tr>
                            </tbody>
                        </Table>
                        <br />
                        <br />
                        <Row className='px-md-5 px-2'>
                            <Col md='6' className='pl-2'>
                                <p><b>Client Info</b></p>
                                <p>Name : {InvoiceDetails.invoice_info.clientinfo.name}</p>
                                <p>Phone : 0{InvoiceDetails.invoice_info.clientinfo.phone}</p>
                                <p>Property : {String(InvoiceDetails.invoice_info.clientinfo.property).replace(/\s*,\s*/g, ",").replace(/,+/g, ',')}</p>
                                <p>Email : {InvoiceDetails.invoice_info.clientinfo.email}</p>
                            </Col>
                            <Col md='6' className='pl-2'>
                                <p><b>Merchant Info</b></p>
                                <p>Name : {InvoiceDetails.invoice_info.merchantinfo.name}</p>
                                <p>Phone : 0{InvoiceDetails.invoice_info.merchantinfo.phone}</p>
                                <p>Address : {String(InvoiceDetails.invoice_info.merchantinfo.address.replace(/\s*,\s*/g, ",")).replace(/,+/g, ',')}</p>
                                <p>Email : {InvoiceDetails.invoice_info.merchantinfo.email}</p>
                            </Col>
                        </Row>
                        <br />
                        <div className="text-center">
                            <Badge pill color={'primary'} className='px-3 py-1'>
                                Payment Info
                            </Badge>
                        </div>
                        <br />
                        <Table responsive bordered >
                            <thead >
                                <tr >
                                    <th className='bg-white'>Tenants</th>
                                    <th className='bg-white'>Description</th>
                                    <th className='bg-white'>Payable for Month</th>
                                    <th className='bg-white'>Per Unit</th>
                                    {/* <th className='bg-white'>Quantity</th> */}
                                    <th className='bg-white'>Amount</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    InvoiceDetails.invoice_info.iteminfo.map(x => <tr>
                                        <th scope="row">{x.number_of_renter || "0"}</th>
                                        <td>{x.description}</td>
                                        <td>{x.pay_for_date}</td>
                                        <td>{x.unit_per}</td>
                                        {/* <td>{x.number_of_renter}</td> */}
                                        <td>{x.amount}</td>
                                    </tr>)
                                }
                            </tbody>
                        </Table>
                        <div className="d-flex justify-content-end">
                            <div className='py-3 pr-2'>
                                <p className='border-bottom p-1 m-0'><b>Sub-Total</b> : {CURRENCY_SYMBOL} {InvoiceDetails.invoice_info.amountinfo.sub_total}</p>
                                <p className='border-bottom p-1 m-0'><b>Tax</b> : {CURRENCY_SYMBOL} {InvoiceDetails.invoice_info.amountinfo.tax} </p>
                                <p className='bg-primary text-white p-1 m-0'><b>Total</b> : {CURRENCY_SYMBOL} {InvoiceDetails.invoice_info.amountinfo.total}</p>
                            </div>
                        </div>
                    </Card>
                </div>
            </ModalBody>
        </Modal>
    )
}

export default InvoiceModal